.header{position:relative}.header .logo{font-weight:600;font-size:20px}.header .nav{float:right;list-style:none;display:flex;align-items:center;gap:20px}.header .nav li{display:inline;margin-left:25px;cursor:pointer;transition:opacity 500ms ease}.header .shopCartButton{float:right;cursor:pointer;transition:color,transform 500ms ease;margin-left:20px}.header .shopCartButton.active{color:#dc3d3d;transform:scale(1.1)}.header .shopCart{position:absolute;top:50px;right:0;width:450px;background:#fafafa;-webkit-box-shadow:4px 5px 15px -7px #606060;box-shadow:4px 5px 15px -7px #606060;z-index:1000;padding:20px;border-radius:10px}.header .shopCart .empty h2{font-size:20px;margin-bottom:20px}.header .shopCart .item{width:100%;margin-bottom:20px;padding:15px;border-bottom:1px solid #eee;display:flex;align-items:center;gap:15px}.header .shopCart .item img{width:70px;height:70px;object-fit:cover;border-radius:5px;flex-shrink:0;float:none;margin-right:0}.header .shopCart .item .itemContent{flex:1}.header .shopCart .item .itemContent h2{font-size:18px;margin-bottom:8px;color:#222}.header .shopCart .item .itemContent p{color:#797979;font-weight:600;font-size:16px}.header .shopCart .item .deleteIcon{color:#ca5252;float:right;position:relative;top:-25px;cursor:pointer;transition:color,transform 500ms ease}.header .shopCart .item .deleteIcon:hover{color:#d83030;transform:scale(1.5)}.header .shopCart .item::after{content:"";display:table;clear:both}.header .shopCart .summa{float:left;width:100%;font-weight:600;font-size:20px}.header .presentation{margin:50px 0;background:url("../../img/комната.jpg") no-repeat;width:100%;height:500px;background-size:cover;background-position:center center;background-blend-mode:multiply;background-color:#bcbcbc;position:relative}.header .presentation::before{content:"По низким ценам";position:absolute;top:250px;left:52px;width:300px;font-size:15px;font-weight:300;color:#fff}.header .presentation::after{content:"Лучшие товары для вашего дома";position:absolute;top:100px;left:50px;width:300px;font-size:40px;font-weight:300;color:#fff}